export function getInputData() {
  const driverImgInput = document.getElementById("driverImgInput");
  const driverNameInput = document.getElementById("driverName");
  const driverBirthInput = document.getElementById("driverBirth");
  const insuranceProductInput = document.getElementById("insuranceProduct");

  //  input   input value  
  if (
    driverImgInput &&
    driverBirthInput &&
    driverBirthInput &&
    insuranceProductInput
  ) {
    const _ = driverImgInput.files[0];
    const _ = driverNameInput.value;
    const _ = Number(driverBirthInput.value);
    const __ =
      insuranceProductInput.options[insuranceProductInput.selectedIndex].text;

    return [_, _, _, __];
  } else {
    console.log("404 ");
  }
}
